<!-- -->

<template>
  <div class="wxc-demo">
    <scroller class="scroller">
      <text class="buttom" @click="back"></text>
      <category title=""></category>
      <div class="wrapper">
          <div class="type-container">
            <wxc-rich-text class="special-rich"
                          :config-list="configTitleString"></wxc-rich-text>
          </div>
          <div class='biaoqian'>
            <wxc-rich-text class="special-rich"
                          :config-list="configList"
                          @wxcRichTextLinkClick="wxcRichTextLinkClick"></wxc-rich-text>
          </div>
          <div class='line'></div>
          <div class='mainbodydiv'>
            <text class='mainbodytext'>{{content}}</text>
          </div>
      </div>
    </scroller>
  </div>

</template>

<style scoped>
  .wxc-demo {
    position: absolute;
    height:1250px;
    left: 0;
    right: 0;
    background-color: #FFFFFF;
  }
  .scroller {
    flex:1;
  }
  .wrapper {
    padding-bottom: 30px;
    padding-top: 10px;
  }
  .type-container {
    margin-bottom: 20px;
    margin-top: 20px;
    align-items: center;
    width: 750px;
  }
  .special-rich {
    margin-top: 14px;
  }
  .biaoqian{
    margin-bottom: 20px;
    margin-top: 20px;
    margin-left:80px;
    margin-right:80px;
  }
  .line{
    height:2px;
    left:0;
    right:0;
    background-color: blue;
  }
  .mainbodydiv{
    padding-top:10px;
    padding-left:20px;
    padding-right:15px;
    padding-bottom:10px;
    align-items: center;
  }
  .mainbodytext{
    font-size:35px;
  }


</style>
<script>
  import Title from '../weex_ui/title.vue';
  import Category from '../weex_ui/category.vue';
  import { WxcRichText, WxcSpecialRichText } from 'weex-ui';
  import { setTitle } from '../weex_ui/set-nav.js';
  import VueEvent from '@/js/vueevent.js'
  export default {
    components: { Title, Category, WxcRichText, WxcSpecialRichText},
    data: () => ({
      msg:' console  Weex App  Demo ',
      content:"",
      configTitleString: [{
          type: 'text',
          value: '',
          theme: 'black',
          style: {
            fontSize: 45,
          }
        }],
      configList: [{
        type: 'tag',
        value: '',
        style: {
          fontSize: 25,
          color: '#ffffff',
          borderColor: '#3a46f7',
          backgroundColor: 'rgba(58, 70, 247, 0.8)',
          height: 38
        }
      }],
    }),
    created () {
      setTitle('RichText');
      this.add(); 
    },
    methods: {
      wxcRichTextLinkClick (e) {
        console.log(e);
      },
      back(){
        VueEvent.$emit('toNews','show');
        this.$router.go(-1);
      },
      add(){
        for(var i=0;i<100;i++){
          this.content=this.content+this.msg;
      }
      }
    }
  };
</script>


