<!-- -->

<template>
  <div class='homepage'>
    <list>
       <refresh class="refresh" @refresh="onrefresh()" @pullingdown="onpullingdown()" :display="showrefresh">
        <text class="textRefresh">refresh...</text>
       </refresh>
       <cell v-for="item in lists">
        <div class="panel" @click="jump">
          <text class="textList">{{item}}</text>
        </div>
       </cell>
       <loading class="loading" @loading="onloading" :display="showLoading">
        <text class="loadingText" >loading...</text>
       </loading>
    </list>
    <div class="wrapper">
    <wxc-overlay
      :show="show"
      opacity="0"
      ></wxc-overlay>
    </div>
  </div>
</template>
<script>

import { WxcOverlay } from 'weex-ui';
import VueEvent from '@/js/vueevent.js'

const modal = weex.requireModule("modal");
const stream = weex.requireModule("stream");
var navigator = weex.requireModule('navigator')

export default {

  name: "App",
  data() {
    return {
      lists: [],
      showLoading: "hide",
      showrefresh: "hide",
      show: false
    };
  },
  components: { WxcOverlay },
  methods: {
    fetch(event) {
      modal.toast({ message: "loadmore", duration: 2 });
      setTimeout(() => {
        const length = this.lists.length;
        for (let i = length; i < length + 4; i++) {
          this.lists.push(i + 1);
        }
      }, 800);
    },
    onloading(event) {
      modal.toast({ message: "loadmore", duration: 2 });
      this.showLoading = "show";
      this.show= true;
      setTimeout(() => {
        const length = this.lists.length;
        for (let i = length; i < length + 4; i++) {
          this.lists.push(i + 1);
        };
        this.showLoading = "hide"; 
        this.show= false;
      }, 2000);
      
    },
    onrefresh(event) {
      modal.toast({ message: "refresh", duration: 2 });
      this.showrefresh = "show";
      this.show= true;
      setTimeout(() => {
        this.lists = [1, 2, 3, 4, 5,6,7];
        this.showrefresh = "hide";
        this.show= false;
      }, 2000);
    },
    onpullingdown(event) {
      modal.toast({ message: "pulling down", duration: 2 });
    },
    getNews(url, callback) {
      return stream.fetch(
        {
          method: "GET",
          type: "json",
          url: url
        },
        callback
      );
    },
    jump (event) {
        this.$router.push('/weextext');
        VueEvent.$emit('toNews','hide');
      },
  },
  created() {
    this.lists = [1, 2, 3, 4, 5, 6, 7];
    modal.toast({ message: "", duration: 1 });
  }
};
</script>
<style scoped>
.panel {
  width: 720px;
  height: 200px;
  margin-left: 15px;
  margin-right: 15px;
  margin-top: 2px;
  margin-bottom: 2px;
  flex-direction: column;
  justify-content: center;
  border-width: 2px;
  border-style: solid;
  border-color: rgb(162, 217, 192);
  background-color: rgba(162, 217, 192, 0.2);
}
.textList {
  font-size: 50px;
  text-align: center;
  color: #418883;
}
.loadingText {
  font-size: 35px;
  text-align: center;
}
.textRefresh {
  font-size: 35px;
  text-align: center;
}
.homepage{
  width: 750px;
  height:1040px;
}
</style>



