<!-- -->

<template>
  <div class="container">
    <div class="demo">
      <wxc-minibar title=""
                   background-color="#009ff0"
                   text-color="#FFFFFF"
                   leftButton=""
                   right-text=""
                   @wxcMinibarLeftButtonClicked="minibarLeftButtonClick"
                   @wxcMinibarRightButtonClicked="minibarRightButtonClick"></wxc-minibar>
    </div>
    <wxc-popup popup-color="rgb(230, 230, 230)"
               :show="isBottomShow"
               @wxcPopupOverlayClicked="popupOverlayBottomClick"
               pos="top"
               height="400">
      <div class="demo-content">
        <div class="demo-content-next">
          <wxc-button class="buttonsize"
                      text=""
                      :textStyle="textStyle"
              @wxcButtonClicked="wxcButtonClicked"></wxc-button>
          <wxc-button class="buttonsize"
                      text=""
                      :textStyle="textStyle"
              @wxcButtonClicked="wxcButtonClicked"></wxc-button>
          <wxc-button class="buttonsize"
                      text=""
                      :textStyle="textStyle"
              @wxcButtonClicked="wxcButtonClicked"></wxc-button>
        </div>
        <div class="demo-content-next">
          <wxc-button class="buttonsize"
                      text=""
                      :textStyle="textStyle"
              @wxcButtonClicked="wxcButtonClicked"></wxc-button>
          <wxc-button class="buttonsize"
                      text=""
                      :textStyle="textStyle"
              @wxcButtonClicked="wxcButtonClicked"></wxc-button>
          <wxc-button class="buttonsize"
                      text=""
                      :textStyle="textStyle"
              @wxcButtonClicked="wxcButtonClicked"></wxc-button>
        </div>
      </div>
    </wxc-popup>
  </div>
</template>

<script>
  import { WxcMinibar } from 'weex-ui';
  import { WxcPopup } from 'weex-ui';
  import { WxcButton } from 'weex-ui';
  const modal = weex.requireModule('modal');

  export default {
    components: { WxcMinibar ,WxcPopup ,WxcButton},
    data: () => ({
      isBottomShow: false,
      height: 400,
      btnStyle: {
        backgroundColor: '#19be6b'
      },
      textStyle:{
      color: '#000000' ,
      fontSize: '40px'
      },
    }),
    methods: {
      minibarLeftButtonClick () {
      },
      minibarRightButtonClick () {
        this.openBottomPopup ();
      },
      openBottomPopup () {
        this.isBottomShow = true;
      },
      //
      popupOverlayBottomClick () {
        this.isBottomShow = false;
      },
      wxcButtonClicked (e) {
        modal.toast({message:'1',duration:2});
      },
    }
  };
</script>

<style scoped>
  .buttonsize{
    height:70px;
    width:210px;
    margin-left: 15px;
    margin-right: 15px;
    margin-top: 15px;
    margin-bottom: 15px;
    background-color:#cbcbcb;
  }
  .demo-content-next{
    direction:ltr;
    flex-direction:row;
    margin-left: 15px;
    margin-right: 15px;
  }
  .container{
    height:100px;
  }
</style>


